@import "../../core/variables";

.share {
  &__button {
    width: 199px;
    height: 50px;
    background-color: $white-three;
    border: none;
    border-radius: 12px;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
    color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px auto 30px auto;
    z-index: 20;
    &--create {
      background-color: $dull-orange;
    }
    &--icon {
      font-size: 22px;
    }
    &--text {
      font-family: $font-generalTexts;
      font-size: 18px;
      text-transform: uppercase;
      font-weight: 600;
      margin-left: 9px;
    }
  }

  .paragraph {
    font-family: $font-generalTexts;
  }
}

.share__finish {
  border-top: 1px solid $white-two;
  padding: 20px 0 30px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &--title {
    font-family: $font-generalTexts;
    font-size: 18px;
    font-weight: 600;
    color: $gunmetal;
  }
  &--text {
    font-family: $font-generalTexts;
    font-weight: 600;
    font-size: 12px;
    text-align: center;
    color: $dull-orange;
    margin-top: 5px;
  }
  &--anchor {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $button-background;
    color: $white;
    width: 181px;
    height: 30px;
    border-radius: 3px;
    text-decoration: none;
    margin-top: 10px;
    .anchor-icon {
      font-size: 18px;
    }
    .anchor-text {
      font-family: $font-generalTexts;
      font-size: 14px;
      line-height: 20px;
      margin-left: 9.5px;
    }
  }
}

.card-link {
  text-align: center;
  text-decoration: none;
  color: $dull-orange;
  font-family: $font-generalTexts;
  font-size: 12px;
  margin: 5px 0px 10px 0px;
}

.js__finish-hidden {
  display: none;
}
